@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600&display=swap);
.login-wrap{height:100%;background:#FF416C;background:linear-gradient(to right, #FF4B2B, #FF416C);display:flex;justify-content:center}.login-wrap main{padding:0 1em 0 1em;margin-top:120px;background:white;width:400px;height:-webkit-max-content;height:max-content;transition:all 1s ease}.login-wrap main .main-head{padding:1em}.login-wrap main .main-head h1{text-align:center}.login-wrap main .toggle{width:-webkit-max-content;width:max-content;margin:auto;padding-bottom:1em;display:flex;align-items:center;grid-gap:1em;gap:1em}.login-wrap main .toggle .tog-option{width:50%;display:flex;align-items:center;justify-content:center;padding:0.75em 1.5em;cursor:pointer;box-shadow:inset 0px 0px 0px 1px #c5c5c5;border-radius:4px;background:white;transition:all 0.15s ease}.login-wrap main .toggle .tog-option:first-child{border-right:none}.login-wrap main .toggle .tog-option:hover{-webkit-filter:brightness(0.95);filter:brightness(0.95)}.login-wrap main .toggle .active{background:#FF416C;background:linear-gradient(to right, #FF4B2B, #FF416C);color:white;box-shadow:none}.login-wrap main .toggle .active:hover{-webkit-filter:brightness(1) !important;filter:brightness(1) !important;background:linear-gradient(to left, #FF4B2B, #FF416C)}.login-wrap main form{margin-bottom:1em;padding:0 1em}.login-wrap main form input{display:block;width:100%;padding:0.5em;margin-bottom:4px;border-radius:4px;border:1px solid #c5c5c5}.login-wrap main form input:focus{outline:none;border:1px solid #FF416C}.login-wrap main form button{display:block;width:100%;cursor:pointer;padding:0.5em 1em;transition:all 0.15s ease;background:#FF416C;background:linear-gradient(to right, #FF4B2B, #FF416C);border:none;border-radius:4px;color:white}.login-wrap main form button:hover{-webkit-filter:brightness(1) !important;filter:brightness(1) !important;background:linear-gradient(to left, #FF4B2B, #FF416C)}.login-wrap main .errors{padding:0 1em 1em 1em}.login-wrap main .errors ul{list-style:none}.login-wrap main .errors ul li{color:#b92749}

.dash-wrap{min-height:100%;background:#FF416C;background:linear-gradient(to right, #FF4B2B, #FF416C) no-repeat;color:white;display:flex;justify-content:center;padding:1em}.dash-wrap .inner-wrap{width:100%;max-width:1000px}.dash-wrap .inner-wrap header{display:flex;justify-content:space-between;align-items:center}.dash-wrap .inner-wrap header button{background:none;border:1px solid white;padding:0.75em 2em;color:white;border-radius:4px;cursor:pointer;transition:all 0.2s ease}.dash-wrap .inner-wrap header button:hover{background:white;color:#FF416C}.dash-wrap .inner-wrap main{padding-top:0.75em}.dash-wrap .inner-wrap main a{text-decoration:none}.dash-wrap .inner-wrap main button{display:flex;align-items:center;justify-content:space-between;padding:0.75em 1.5em;background:white;color:#FF416C;border:1px solid white;border-radius:4px;cursor:pointer;transition:all 0.2s ease}.dash-wrap .inner-wrap main button:hover{border:1px solid white;background:none;color:white}.dash-wrap .inner-wrap main button:hover svg{stroke:white}.dash-wrap .inner-wrap main button svg{display:block;margin-left:4px;transition:all 0.2s ease}.dash-wrap .inner-wrap main .post-area{padding-top:1em}.dash-wrap .inner-wrap main .post-area .post-nav{display:flex;align-items:center;justify-content:center;grid-gap:0.5em;gap:0.5em}.dash-wrap .inner-wrap main .post-area .post-nav .nav-li{display:flex;align-items:center;justify-content:center;padding:3px;border-radius:16px;border:1px solid white;min-width:32px;height:32px;transition:all 0.2s ease;cursor:pointer}.dash-wrap .inner-wrap main .post-area .post-nav .nav-li:hover{background:white !important;color:#FF416C !important}

.post{display:flex;justify-content:space-between;align-items:stretch;background:white;border-radius:8px;margin-bottom:1em;color:black}.post .post-content{flex:1 1;cursor:pointer;transition:all 0.2s ease;background:white;border-radius:8px}.post .post-content:hover{-webkit-filter:brightness(0.96);filter:brightness(0.96)}.post .post-content .content-header{display:flex;align-items:center;justify-content:space-between;padding:1em;border-bottom:1px solid #FF416C;color:#FF4B2B;font-size:16px}.post .post-content .content-header .post-title{color:#FF4B2B}.post .post-content .post-content-area{padding:1em;white-space:pre-wrap}


.create-wrap{min-height:100%;background:#FF416C;background:linear-gradient(to right, #FF4B2B, #FF416C) no-repeat;color:white;display:flex;justify-content:center;padding:1em}.create-wrap .inner-wrap{width:100%;max-width:1000px}.create-wrap .inner-wrap header{display:flex;justify-content:space-between;align-items:center;margin-bottom:1em}.create-wrap .inner-wrap header button{background:none;border:1px solid white;padding:0.75em 2em;color:white;border-radius:4px;cursor:pointer;transition:all 0.2s ease}.create-wrap .inner-wrap header button:hover{background:white;color:#FF416C}.create-wrap .inner-wrap .post{display:flex;justify-content:space-between;align-items:stretch;background:white;border-radius:8px;margin-bottom:1em;color:black}.create-wrap .inner-wrap .post .post-content{flex:1 1;cursor:auto !important;transition:all 0.2s ease;background:white;border-radius:8px}.create-wrap .inner-wrap .post .post-content:hover{-webkit-filter:brightness(0.96);filter:brightness(0.96)}.create-wrap .inner-wrap .post .post-content .content-header{display:flex;align-items:center;justify-content:space-between;padding:1em;border-bottom:1px solid #FF416C;color:#FF4B2B;font-size:16px}.create-wrap .inner-wrap .post .post-content .content-header .post-title{color:#FF4B2B}.create-wrap .inner-wrap .post .post-content .post-content-area{padding:1em;white-space:pre-wrap}.create-wrap .inner-wrap .comment-form{display:block;width:100%;padding:1em;padding-bottom:0em;margin-bottom:1em;background:white;border-radius:8px}.create-wrap .inner-wrap .comment-form textarea{padding:8px;margin-bottom:1em;border:1px solid #cecece;border-radius:8px;display:block;width:100%;resize:none;overflow:hidden;min-height:50px;max-height:800px}.create-wrap .inner-wrap .comment-form textarea:focus{outline:none}.create-wrap .inner-wrap .comment-form button{border:1px solid #FF4B2B;background:#FF4B2B;color:white}.create-wrap .inner-wrap .comment-cont .comment{border-radius:8px;background:white;color:black;margin-bottom:8px}.create-wrap .inner-wrap .comment-cont .comment .comment-head{padding:0.5em;display:flex;align-items:center;justify-content:space-between;border-bottom:solid 1px #FF416C;font-size:14px}.create-wrap .inner-wrap .comment-cont .comment .comment-head h3{font-weight:400 !important}.create-wrap .inner-wrap .comment-cont .comment .comment-content{padding:0.5em;font-size:14px}.create-wrap .inner-wrap button{background:none;padding:1em 2em;border-radius:8px;border:1px solid white;color:white;cursor:pointer;transition:all 0.2s ease;margin-bottom:1em}.create-wrap .inner-wrap button:hover{color:#FF416C;background:white}

*{margin:0;padding:0;box-sizing:border-box;font-family:'Poppins', sans-serif}html,body,#root,.App{min-height:100%;height:100%}

